import React from 'react'
import WindowsLogo from '../../images/WindowsLogo.svg'

class TaskBar extends React.Component{
    render() {
        const iconList = this.props.icons.map((item) => <div className="TaskBarItem">
                                                            <img className="TaskBarImg" src={item}/>
                                                        </div>)

        return(
            <div className="TaskBar">
                <div className="TaskBarItem">
                    <img className="TaskBarImg" src={WindowsLogo}/>
                </div>
                <form>
                    <input type="text" placeholder="Type here to search" />
                </form>
                {iconList}
            </div>
        )
  }
}

export default TaskBar;