import React from 'react'
import { CSSTransition } from 'react-transition-group'

import TaskBar from './TaskBar'
import FirefoxLogo from '../../images/FirefoxLogo.svg'
import VSCodeLogo from '../../images/VSCodeLogo.svg'
import NotesLogo from '../../images/NotesLogo.svg'
import WindowsDesktop from '../../images/WindowsDesktop.svg'
import PortfolioPage from './PortfolioPage'



class UnlockedScreen extends React.Component{
    constructor(props) {
        super(props)
        this.state = {
            Icons: [
                VSCodeLogo,
                NotesLogo
            ],
            terminal: "$ ",
            displayFirefox: false
        }

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    
    handleChange(e) {
        this.setState({terminal: e.target.value})
    }

    handleSubmit(e) {
        e.preventDefault()
        if (this.state.terminal === "$ npm deploy Portfolio") {
            this.setState({Icons: [VSCodeLogo, NotesLogo, FirefoxLogo]})
            this.setState({displayFirefox: true})
        }
    }

    render() {
    return(
      <React.Fragment>
          <img className="WindowsDesktop" src={WindowsDesktop} />
          <div className="VSCodeWindow">
                <div className="VSCodeSidebar"></div>
                <div className="VSCodeHeader">
                    <div className="CloseButton"></div>
                </div>
                <div className="VSCodeText">
                <span className="purple">import </span><span className="blue">React </span><span className="purple">from </span><span className="orange">'react'</span><br></br>
                <span className="purple">import </span><span className="blue">Portfolio </span><span className="purple">from </span><span className="orange">'portfolio'</span><br></br>
                <br></br>
                <span className="green">class </span><span className="darkblue">PortfolioWebsite </span><span className="green">extends </span><span className="darkblue">React.Component </span>{`{`}<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;<span className="yellow">render</span>() {`{`}<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span className="purple">return(</span><br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&lt;<span className="green">Portfolio</span> /&gt;<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;)<br></br>
                &nbsp;&nbsp;&nbsp;&nbsp;{`}`}<br></br>
                {`}`}<br></br>
                <br></br>
                <span className="purple">export </span><span className="purple">default </span><span className="green">PortfolioWebsite </span>  
                </div>
                <form className="VSCodeTerminal" onSubmit={this.handleSubmit}>
                    <label><u>TERMINAL</u><br></br></label>
                    <label>Luke Halberstadt MINGW64 /d/Portfolio</label>
                    <input type="text" value={this.state.terminal} onChange={this.handleChange}/>
                </form>
          </div>
          <div className="NotepadWindow">
                <div className="NotepadHeader">
                    <div className="CloseButton"></div>
                </div>
                <div className="NotepadText">
                    <b>TO-DO:</b><br></br>
                    Launch website!<br></br>
                    <br></br>
                    <br></br>
                    <b>REMINDER:</b><br></br>
                    To launch website, type into the terminal:<br></br>
                    npm deploy Portfolio
                </div>
          </div>
          <CSSTransition 
            in={this.state.displayFirefox}
            timeout={150}
            classNames="FirefoxOpen">
            <PortfolioPage />
          </CSSTransition>
          <TaskBar icons={this.state.Icons}/>
      </React.Fragment>
    )
  }
}

export default UnlockedScreen;

