import React from 'react'

class HelloText extends React.Component{
  render() {
    return(
      <svg width="900" height="300" viewBox="0 0 500 500" xmlns="http://www.w3.org/2000/svg"
           className="HelloText">
            <defs>
                <style type="text/css">
                    @import url('https://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300italic,400italic,500,500italic,700,700italic,900,900italic');
                </style>
            </defs>

            <text x="50%" y="15%" dominantBaseline="middle" textAnchor="middle" fontSize="80" fill="white" fontWeight="100" fontFamily="Roboto">
                Hello there
                <animate attributeName="opacity" dur="10s" repeatCount="1" keyTimes="0; 0.5; 0.55; 1;" values="0; 0; 1; 1;" />
                <animate attributeName="y" dur="10s" repeatCount="1" keyTimes="0; 0.5; 0.55; 1;" values="17%; 17%; 15%; 15%;" />
            </text>
            <text x="50%" y="45%" dominantBaseline="middle" textAnchor="middle" fontSize="80" fill="white" fontWeight="100" fontFamily="Roboto">
                My name is Luke Halberstadt
                <animate attributeName="opacity" dur="10s" repeatCount="1" keyTimes="0; 0.7; 0.75; 1;" values="0; 0; 1; 1;" />
                <animate attributeName="y" dur="10s" repeatCount="1" keyTimes="0; 0.7; 0.75; 1;" values="47%; 47%; 45%; 45%;" />
            </text>
            <text x="50%" y="75%" dominantBaseline="middle" textAnchor="middle" fontSize="80" fill="white" fontWeight="100" fontFamily="Roboto">
                Welcome to my portfolio
                <animate attributeName="opacity" dur="10s" repeatCount="1" keyTimes="0; 0.9; 0.95; 1;" values="0; 0; 1; 1;" />
                <animate attributeName="y" dur="10s" repeatCount="1" keyTimes="0; 0.9; 0.95; 1;" values="77%; 77%; 75%; 75%;" />
            </text>

      </svg>
    )
  }
}

export default HelloText;