import React from 'react'
import LockScreenSVG from '../../images/LockScreen.svg'
import Hello from './Hello';
import HelloText from './HelloText';
import UnlockedScreen from './UnlockedScreen';

class LockScreen extends React.Component{
  render() {
    return(
      <div className="LockScreenCon">
          <UnlockedScreen />
          <div className="LockScreenMover">
            <img src={LockScreenSVG} className="LockScreenBG"></img>
            <Hello />
            <HelloText />
          </div>
      </div>
    )
  }
}

export default LockScreen;