import React from 'react'
import ParallaxCharacters from '../../images/ParallaxCharacters.svg'
import ScamCheck from '../../images/ScamCheck.svg'
import RLPlaza from '../../images/RLPlaza.svg'
import ScamCheckLogo from '../../images/ScamCheckLogo.svg'
import RLPlazaLogo from '../../images/RLPlazaLogo.svg'


class PortfolioPage extends React.Component {
    render() {
        return(
            <div className="FirefoxWindow">
                <div className="FirefoxHeaderUpper">
                    <div className="CloseButton"></div>
                    <div className="FirefoxHeaderLower"></div>
                </div>
                <div className="FirefoxPage">
                    <div className="PortfolioItem StatsExpress" onClick={() => window.open("https://lukehalby.github.io/StatsExpressPages", "_blank")}>
                        <div className="LogoCon">
                            <p className="SETitle1">STATS EXPRESS</p>
                            <p className="SETitle2">VALORANT</p>
                        </div>
                        <div className="ImageCon">
                            <img className="StatsExpressImg" src={ParallaxCharacters} />
                        </div>
                    </div>
                    <div className="PortfolioItem ScamCheck" onClick={() => window.open("https://lukehalby.github.io/ScamCheckPages", "_blank")}>
                        <div className="LogoCon">
                            <img src={ScamCheckLogo} />
                            <p className="SCTitle">ScamCheck</p>
                        </div>
                        <div className="ImageCon">
                            <img className="ScamCheckImg" src={ScamCheck} />
                        </div>
                    </div>
                    <div className="PortfolioItem RLPlaza" onClick={() => window.open("https://lukehalby.github.io/rlplaza", "_blank")}>
                        <div className="LogoCon">
                            <img src={RLPlazaLogo} />
                            <p className="RLTitle">RL Plaza</p>
                        </div>
                        <div className="ImageCon">
                            <img className="RLPlazaImg" src={RLPlaza} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default PortfolioPage